.carousel-turn-table-landing{
    margin: 0px;
    width: auto;
    justify-content: center;
    align-self: center;
    display: block;
}


.land-carousel-space-1{
    background-image: url("../assets/Chihuahua2011.png");
    background-size: cover;
    width: auto;
    height: 550px;
}

.image-content-carousel-1{
    color: rgb(255, 255, 255, 1);
    background-color: rgb(0, 0, 0, 0.3);
    padding-left: 15px;
    text-align: left;
}

.image-header-carousel-1{
    font-size: 55px;
    font-weight: 800;
    height: 50px;
    padding-bottom: 0px;
    margin-bottom: 30px;
    display: inline-block;
}

.image-text-carousel-1{
    font-size: 38px;
    font-weight: 600;
    margin-top: 2px;
}


.land-carousel-space-2{
    background-image: url("../assets/Hermosillo2011.png");
    background-size: cover;
    width: auto;
    height: 550px;
}

.image-content-carousel-2{
    color: rgb(255, 255, 255, 1);
    background-color: rgb(0, 0, 0, 0.3);
    padding-right: 15px;
    text-align: right;
}

.image-header-carousel-2{
    font-size: 55px;
    font-weight: 800;
    height: 50px;
    padding-bottom: 0px;
    padding-top: 0;
    margin-bottom: 30px;
    display: inline-block;
}

.image-text-carousel-2{
    font-size: 38px;
    font-weight: 600;
    margin-top: 2px;
}